.label {
	margin: 7px 0;
	color: var(--hrm_palette-imput-label_gray);
	text-align: left;
}


@keyframes load {
	from {transform: translate(0, 0)}
	to {transform: translate(390px, 0)}
}

@keyframes loadcenter {
	0% {
		width: 0;
	}
	50% {
		width: 50%;
	}
	100% {
		width: 100%;
	}
}

.load_animation{
	margin: auto;
	height: 2px;
	background-color: #646cff;
	animation: loadcenter .65s infinite alternate ease-in-out;
}